import { useState, useEffect, useCallback } from 'react'

export const Helper = ()=>{
    const [images, setImages] = useState([])
    const [input, setInput] = useState("")
    const [loading, setloading] = useState(true)

    
    const peticion = useCallback(async ()=>{
            const key =  'client_id=DMHNBe8jjTfrMBLYSLyjsN3aAAl778ipsk4Gvt3fY_M';
            
                let  route = `https://api.unsplash.com/photos/?`; 
        
                if(input === ""){
                    route = `https://api.unsplash.com/photos/?${key}`
                } else{
                    route = `https://api.unsplash.com/search/photos/?query=${encodeURI(input)}&${key}`;
                }

                setloading(true)
                
                const res = await fetch(route);
        
                const data = await res.json();
        
                if(data.results){
                    setImages(data.results)
                }else{
                    setImages(data)
                }
                
                setloading(false)
            },[input] )

    useEffect(() => {
        peticion()
    }, [peticion]);

    

    const handleChange = (e)=>{
        e.preventDefault();
        const text = e.target[0].value;

        setInput(text)
    };

    return [images, loading, handleChange]
}